<template>
  <div class="subcontainer">
    <div class="header secondary-header">
      Freets
    </div>
    <div>
      <div v-if='success' class="success-message">
        {{ success }}
      </div>
      <div v-if='error' class="error-message">
        {{ error }}
      </div>

      <div class="freets-container">  
        <div v-if="freets.length">
          <Freet
          v-for="freet in freets"
          v-bind:key="freet.id"
          v-bind:freet="freet"
        />
        </div>
        <div v-else>
          <p style="text-align: center;">There are no freets to display. Create one today!</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
// import FreetListItem from "./FreetListItem";
import Freet from "./Freet";


import { eventBus } from "../main";

export default {
  name: "FreetList",

  components: { Freet },

  props: {
      user : Object,
      isSignedIn:Boolean,
    },

  data() {
    return {
      error: "",
      success: "",
      freets: [],
    };
  },

  created: function() {
    // PROBLEM HERE!
    eventBus.$on("create-freet-success", res => {
      this.freets.push(res.data);
    });

    eventBus.$on("update-freet-success", res => {
      this.success = `Freet name ${res.data.freetName} now resolves to ${
        res.data.url
      }`;
      this.clearMessages();
      this.loadFreets();
    });

    eventBus.$on("delete-freet-success", res => {
      this.success = `Freet name ${res.freetName} has been deleted`;
      this.clearMessages();
      this.loadFreets();
    });

    // eventBus.$on("update-freet-error", res => {
    //   this.error = `Error updating freet ${res.data.freetName}`;
    //   this.clearMessages();
    //   this.loadFreets();
    // });

    // eventBus.$on("delete-freet-error", res => {
    //   this.error = `Error deleting freet ${res.freetName}`;
    //   this.clearMessages();
    //   this.loadFreets();
    // });
  },

  mounted: function() {
    this.loadFreets();
  },

  methods: {
    loadFreets: function() {
      axios
      .get("/api/freets").then(response => {
        this.freets = response.data;
      })
      // .then((res) => {
      //     // handle success
      //     eventBus.$emit("update-short-success", res);
      //   });
    },

    clearMessages: function() {
      setInterval(() => {
        this.success = "";
        this.error = "";
      }, 5000);
    }
  }
};
</script>

